@value grindWhite, grindBlack from '../../common/colors.css';

.button {
  background-color: grindWhite;
  outline: none;
  border: none;
  border-top: 1px solid black;
  border-left: 1px solid black;
  padding: 0.6em 1.5em;
  cursor: pointer;
  opacity: 0.7;
}

.button:hover {
  opacity: 1;
}
